<template>
  <div>
    <vxe-grid
      border
      resizable
      ref="menuTable"
      :toolbar-config="toolBarConfig"
      :row-config="{isCurrent: true}"
      :columns="tableColumn"
      :data="tableData">
      <template #toolbar_buttons>
        <vxe-input v-model="searchName" placeholder="请输入菜单名称" clearable></vxe-input>
        <vxe-button status="primary" @click="query(searchName)">搜索</vxe-button>
        <vxe-button status="success" @click="addEvent">新增</vxe-button>
        <vxe-button status="success" @click="editEvent">修改</vxe-button>
        <vxe-button status="success" @click="removeEvent">删除</vxe-button>
        <vxe-button @click="$refs.menuTable.exportData()">导出</vxe-button>
      </template>
      <template #pager>
        <vxe-pager
          :layouts="['Sizes', 'PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'FullJump', 'Total']"
          :current-page.sync="tablePage.currentPage"
          :page-size.sync="tablePage.pageSize"
          :total="tableData.length"
          @page-change="handlePageChange">
        </vxe-pager>
      </template>
    </vxe-grid>
    <add-menu-form></add-menu-form>
    <edit-menu-form></edit-menu-form>
  </div>
</template>

<script>
import {error} from '../../../api/error'
import axios from 'axios'
import EditMenuForm from './editMenuForm'
import bus from '../../../common/bus'
import AddMenuForm from './addMenuForm'
import {interceptor} from '../../../api/interctor'

export default {
  name: 'menuList',
  components: {AddMenuForm, EditMenuForm},
  data () {
    return {
      toolBarConfig: {
        slots: {
          buttons: 'toolbar_buttons'
        }
      },
      form_name: '',
      form_id: -1,
      searchName: '',
      tablePage: {
        currentPage: 1,
        pageSize: 10
      },
      tableColumn: [
        {field: 'menu_name', title: '菜单名称'},
        {field: 'menu_level', title: '菜单级别'},
        {field: 'father_menu', title: '父菜单'},
        {field: 'context_form', title: '关联表单'}
      ],
      tableData: []
    }
  },
  created () {
    this.query('')
    bus.$on('refreshMenu', () => {
      this.query('')
    })
  },
  methods: {
    async query (val) {
      try {
        const res = await this.$http.get('/api/menu/query', {params: {menu_name: val}})
        if (res.data.code !== 200) {
          interceptor(res.data.msg)
        } else {
          this.tableData = res.data.data
        }
      } catch (e) {
        error(e)
      }
    },
    addEvent () {
      bus.$emit('showAddMenuForm')
    },
    handlePageChange ({currentPage, pageSize}) {
      this.tablePage.currentPage = currentPage
      this.tablePage.pageSize = pageSize
    },
    removeEvent () {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          const selectRecords = this.$refs.menuTable.getCurrentRecord()
          const res = await axios.post('api/menu/delete', {
            menu_id: selectRecords.menu_id
          })
          if (res.data.code !== 200) {
            interceptor(res.data)
          } else {
            this.tableData = res.data.data
          }
        } catch (e) {
          error(e.message)
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    editEvent () {
      const selectRecords = this.$refs.menuTable.getCurrentRecord()
      if (!selectRecords) {
        return error('请先选择需要修改的数据')
      }
      bus.$emit('showEditMenuForm', selectRecords)
    }
  }
}
</script>

<style scoped>

</style>



